import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout from "@components/layout";
import Seo from "@components/seo";
import BreadCrumb from "@components/common/breadcrumb";
import StockTradingScreen from "@screens/stock-trading";
import { useLocation } from "@reach/router";

const StockTradingPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Stock Trading App Development", link: null },
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <StockTradingScreen db={db} />
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const fileUrl =
    data?.stockTradeImg?.childImageSharp?.gatsbyImageData?.images?.fallback?.src;
  const location = useLocation();
  return (
    <Seo
      title="Stock Trading App Development Company | Justtry Technologies"
      metaTitle="Stock Trading App Development Company | Justtry Technologies"
      description="Justtry Tech is a leading stock trading application development service provider in India, USA. We enhance your trading platform with advanced functions and features like stock search, portfolio management, watchlist, buy and sell, etc.,"
      image={fileUrl}
      siteFirestore={data?.site?.siteMetadata?.firestore}
      slug={location.pathname.split("/")?.[1]}
    ></Seo>
  );
};

export default StockTradingPage;

export const queryData = graphql`
  query MyQuery {
    stockTradeImg: file(relativePath: { eq: "stock-trading.png" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
